



































































import { Component, Vue } from 'vue-property-decorator';
import Header from '@/components/Header.vue';
import AgentExclusiveList from '@/components/Agent/AgentExclusiveList.vue';

@Component({
  components: {
    Header,
    AgentExclusiveList,
  },
})

export default class Agent extends Vue {
  private pageTitle?: String = '代理中心';
  private agentName?: String = 'anxin520';
  private landingTime?: String = ' 2020.02.01 13:59:01';
  private list?: any = [];
  private loading?: Boolean = false;
  private finished?: Boolean = false;
  private refreshing?: Boolean = false;
  private isActive?: any = 1;
  private isMultiLevel?: any = {
    bool: true, value: 3
  };

  private addClass() {
    this.isActive = !this.isActive
  }

  private onLoad() {
    setTimeout(() => {
      if (this.refreshing) {
        this.list = [];
        this.refreshing = false;
      }

      for (let i = 0; i < 10; i++) {
        this.list.push(this.list.length + 1);
      }
      this.loading = false;

      if (this.list.length >= 40) {
        this.finished = true;
      }
    }, 1000);
  }
  private onRefresh() {
    this.finished = false;
    this.loading = true;
    this.onLoad();
  }
}
